import React from "react";
import { Spinner } from "react-bootstrap";
import { MDBTable, MDBTableBody, MDBTableHead } from "mdbreact";
import { Input } from "reactstrap";
import Pagination from "../Pagination/Pagination";
import Select from "react-select";
import "./DataTable.scss";
import Loader from "../Loader";

const dataTablePaginationOptions = [10, 20, 50, 100].map((paginationValue) => {
  return {
    label: String(paginationValue),
    value: isNaN(paginationValue) ? 10 : paginationValue,
  };
});

class DataTable extends React.Component {
  /**
   * This will handle request data for calling list API
   * @param searchValue {string}
   * @param pageNo {number}
   * @param pageSize {number}
   * @param sortAs {string}
   * @param sortBy {string}
   * @return {{pageNo: *, pageSize: *, searchValue: *}}
   */
  generateRequestData = (searchValue, pageNo, pageSize, sortAs, sortBy) => {
    let data = {
      searchValue: searchValue,
      pageNo: pageNo,
      pageSize: pageSize,
    };
    if (sortAs && sortBy) {
      data.sortAs = sortAs;
      data.sortBy = sortBy;
    }
    return data;
  };

  /**
   * This will handle onSearch table event
   * @param event
   */
  onSearchTable = (event) => {
    let query = event.target.value;
    const { searchValue, callListAPI, pageSize, sortAs, sortBy } = this.props;
    if (query !== searchValue) {
      clearTimeout(this.timer);
      this.timer = setTimeout(
        () =>
          callListAPI(
            this.generateRequestData(query, 0, pageSize, sortAs, sortBy)
          ),
        400
      );
    }
  };

  /**
   * This will handle limit change
   * @param pageSize {number}
   */
  limitChange = (pageSize) => {
    const { sortAs, searchValue, callListAPI, sortBy } = this.props;
    callListAPI(
      this.generateRequestData(searchValue, 0, pageSize, sortAs, sortBy)
    );
  };

  /**
   * This will handle page change value
   * @param data
   */
  onPageChanged = (data) => {
    const { currentPage } = data;
    const { searchValue, callListAPI, pageSize, sortAs, sortBy } = this.props;
    callListAPI(
      this.generateRequestData(
        searchValue,
        currentPage - 1,
        pageSize,
        sortAs,
        sortBy
      )
    );
  };

  /**
   * This will handle sorting
   * @param sortAs {string}
   * @param sortBy {string}
   */
  handleSorting = (sortAs, sortBy) => {
    const { searchValue, callListAPI = () => {}, pageSize } = this.props;
    callListAPI(
      this.generateRequestData(searchValue, 0, pageSize, sortAs, sortBy)
    );
  };

  /**
   * This will generate table columns
   * @param columns {[{label : string, field : string, sortValue : string}]}
   * @return {*[]}
   */
  generateTableHeader = (columns = []) => {
    const { sortAs, sortBy } = this.props;
    let ths = [];

    columns.map((column, index) => {
      const { style = {}, sortValue = undefined, label = undefined } = column;
      let sortIcon = "fas fa-sort ml-1";
      let nextSortBy = "DESC";
      if (sortBy === sortValue) {
        if (sortAs === "ASC") {
          sortIcon = "fas fa-sort-up ml-1";
        } else {
          nextSortBy = "ASC";
          sortIcon = "fas fa-sort-down ml-1";
        }
      }
      ths.push(
        <th key={index} style={{ ...style }}>
          {label}
          {sortValue && (
            <div role="button" className={"pull-right"}>
              <i
                className={sortIcon}
                onClick={() => this.handleSorting(nextSortBy, sortValue)}
              />
            </div>
          )}
        </th>
      );
      return column;
    });
    return ths;
  };

  render() {
    const {
      totalCounts,
      startEntry,
      pageSize,
      pageNo,
      isGetLoading,
      tableData,
      totalPages,
    } = this.props;
    return (
      <>
        <div className={"d-flex"}>
          <div style={{ width: "150px" }}>
            <label className={"mt-2 me-1"}>Show entries</label>
            <Select
              isClearable={false}
              name="Entries"
              options={dataTablePaginationOptions}
              value={{ label: pageSize, value: pageSize }}
              onChange={(selectedOption) => {
                const nextPageSize =
                  selectedOption && selectedOption.value
                    ? selectedOption.value
                    : 10;
                this.limitChange(nextPageSize);
              }}
            />
          </div>
          <div className={"ms-auto mt-4"}>
            <div className={"d-flex"}>
              <span className={"me-2"}>
                <Input
                  onKeyUp={this.onSearchTable}
                  onBlur={this.onSearchTable}
                  placeholder={"Search"}
                  bsSize={"md"}
                  type={"text"}
                />
              </span>
            </div>
          </div>
        </div>
        <>
          {isGetLoading && (
            <div className="data-table-spinner mt-2">
              <Spinner animation="border" role="status">
                <span className="visually-hidden">Loading...</span>
              </Spinner>
            </div>
          )}
          {!isGetLoading && totalCounts < 1 && (
            <span style={{ textAlign: "center" }}>
              <h3>No Data Available</h3>
            </span>
          )}
          {!isGetLoading && totalCounts >= 1 && (
            <>
              <div>
                <MDBTable
                  striped={true}
                  responsiveSm={true}
                  borderless
                  hover
                  responsive
                >
                  <MDBTableHead>
                    <tr>{this.generateTableHeader(tableData.columns)}</tr>
                  </MDBTableHead>
                  <MDBTableBody rows={tableData.rows} />
                </MDBTable>
              </div>
              <div>
                Showing {startEntry} to{" "}
                {pageSize * (pageNo + 1) < totalCounts
                  ? pageSize * (pageNo + 1)
                  : totalCounts}{" "}
                of {totalCounts} entries
                <span className={"pull-right"}>
                  {totalCounts ? (
                    <Pagination
                      totalRecords={totalCounts}
                      pageLimit={this.props.pageSize}
                      pageNeighbours={1}
                      totalPages={totalPages}
                      onPageChanged={this.onPageChanged}
                      currentPage={this.props.pageNo + 1}
                    />
                  ) : (
                    <></>
                  )}
                </span>
              </div>
            </>
          )}
        </>
      </>
    );
  }
}
DataTable.defaultProps = {
  totalCounts: 0,
  startEntry: 1,
  pageSize: 10,
  pageNo: 0,
  totalPages: 1,
  isGetLoading: false,
  tableData: { rows: [], columns: [] },
  tableHeaders: [],
  sortedColumn: [],
  sortBy: "",
  sortAs: "ASC",
  searchValue: "",
  callListAPI: () => {},
};
export default DataTable;
