/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { Component, Fragment } from "react";
import PropTypes from "prop-types";
import "./Pagination.scss";

const LEFT_PAGE = "LEFT";
const RIGHT_PAGE = "RIGHT";

const range = (from, to, step = 1) => {
  let i = from;
  const range = [];

  while (i <= to) {
    range.push(i);
    i += step;
  }

  return range;
};

class Pagination extends Component {
  constructor(props) {
    super(props);
    const {
      totalRecords = null,
      pageLimit = 10,
      pageNeighbours = 0,
      totalPages = null,
    } = props;

    this.pageLimit = typeof pageLimit === "number" ? pageLimit : 10;
    this.totalRecords = typeof totalRecords === "number" ? totalRecords : 0;

    this.pageNeighbours =
      typeof pageNeighbours === "number"
        ? Math.max(0, Math.min(pageNeighbours, 2))
        : 0;

    this.totalPages = totalPages
      ? totalPages
      : Math.ceil(this.totalRecords / this.pageLimit);

    // this.state = { currentPage: 1 };
    // this.handleClick = this.handleClick.bind(this);
  }

  componentDidMount() {
    // this.gotoPage(1);
  }

  // shouldComponentUpdate(nextProps) {
  //   const differentPageLimit = this.props.pageLimit !== nextProps.pageLimit;
  //   return differentPageLimit;
  // }

  gotoPage = (page) => {
    const { onPageChanged = (f) => f } = this.props;

    const currentPage = Math.max(0, Math.min(page, this.props.totalPages));

    const paginationData = {
      currentPage,
      totalPages: this.props.totalPages,
      pageLimit: this.pageLimit,
      totalRecords: this.totalRecords,
    };
    onPageChanged(paginationData);
    // this.setState({ currentPage }, () => onPageChanged(paginationData));
  };

  handleClick = (page, evt) => {
    evt.preventDefault();
    this.gotoPage(page);
  };

  handleMoveLeft = (evt) => {
    evt.preventDefault();
    this.gotoPage(this.props.currentPage - this.pageNeighbours * 2 - 1);
  };

  handleMoveRight = (evt) => {
    evt.preventDefault();
    this.gotoPage(this.props.currentPage + this.pageNeighbours * 2 + 1);
  };

  fetchPageNumbers = () => {
    const totalPages = this.props.totalPages;
    const currentPage = this.props.currentPage || 1;
    const pageNeighbours = this.pageNeighbours;

    const totalNumbers = this.pageNeighbours * 2 + 3;
    const totalBlocks = totalNumbers + 2;

    if (totalPages > totalBlocks) {
      let pages = [];

      const leftBound = currentPage - pageNeighbours;
      const rightBound = currentPage + pageNeighbours;
      const beforeLastPage = totalPages - 1;

      const startPage = leftBound > 2 ? leftBound : 2;
      const endPage = rightBound < beforeLastPage ? rightBound : beforeLastPage;

      pages = range(startPage, endPage);

      const pagesCount = pages.length;
      const singleSpillOffset = totalNumbers - pagesCount - 1;

      const leftSpill = startPage > 2;
      const rightSpill = endPage < beforeLastPage;

      const leftSpillPage = LEFT_PAGE;
      const rightSpillPage = RIGHT_PAGE;

      if (leftSpill && !rightSpill) {
        const extraPages = range(startPage - singleSpillOffset, startPage - 1);
        pages = [leftSpillPage, ...extraPages, ...pages];
      } else if (!leftSpill && rightSpill) {
        const extraPages = range(endPage + 1, endPage + singleSpillOffset);
        pages = [...pages, ...extraPages, rightSpillPage];
      } else if (leftSpill && rightSpill) {
        pages = [leftSpillPage, ...pages, rightSpillPage];
      }
      return [1, ...pages, totalPages];
    }
    return range(1, totalPages);
  };

  render() {
    if (!this.totalRecords) return null;

    // if (this.props.totalPages === 1) return null;

    const { currentPage, totalPages } = this.props;
    const pages = this.fetchPageNumbers();

    return (
      <Fragment>
        <nav aria-label="Countries Pagination">
          <ul className="pagination">
            {currentPage > 1 && (
              <li key={0} className="page-item">
                <a
                  className="page-link"
                  aria-label="Previous"
                  onClick={(event) => this.handleClick(currentPage - 1, event)}
                >
                  <span aria-hidden="true">Previous</span>
                  <span className="visually-hidden">Previous</span>
                </a>
              </li>
            )}
            {pages.map((page, index) => {
              if (page === LEFT_PAGE)
                return (
                  <li key={index} className="page-item">
                    <a
                      className="page-link"
                      href="#"
                      aria-label="Previous"
                      onClick={this.handleMoveLeft}
                    >
                      <span aria-hidden="true">&laquo;</span>
                      <span className="visually-hidden">Previous</span>
                    </a>
                  </li>
                );

              if (page === RIGHT_PAGE)
                return (
                  <li key={index} className="page-item">
                    <a
                      className="page-link"
                      href="#"
                      aria-label="Next"
                      onClick={this.handleMoveRight}
                    >
                      <span aria-hidden="true">&raquo;</span>
                      <span className="visually-hidden">Next</span>
                    </a>
                  </li>
                );

              return (
                <li
                  key={index}
                  className={`page-item${
                    currentPage === page ? " active" : ""
                  }`}
                >
                  {currentPage === page ? (
                    <a className="page-link" href="#">
                      {page}
                    </a>
                  ) : (
                    <a
                      className="page-link"
                      href="#"
                      onClick={(e) => this.handleClick(page, e)}
                    >
                      {page}
                    </a>
                  )}
                </li>
              );
            })}
            {currentPage < totalPages && (
              <li key={1} className="page-item">
                <a
                  className="page-link"
                  aria-label="Previous"
                  onClick={(event) => this.handleClick(currentPage + 1, event)}
                >
                  <span aria-hidden="true">Next</span>
                  <span className="visually-hidden">Next</span>
                </a>
              </li>
            )}
          </ul>
        </nav>
      </Fragment>
    );
  }
}

Pagination.propTypes = {
  totalRecords: PropTypes.number.isRequired,
  pageLimit: PropTypes.number,
  pageNeighbours: PropTypes.number,
  onPageChanged: PropTypes.func,
};

export default Pagination;
